.content {
  position: relative;
  background-color: #000000;
  max-width: 880px;
  margin: 45px auto 0;
  font-family: "Inter", Arial, sans-serif;
}

@media (max-width: 425px) {
  .content {
    margin-top: 25px;
  }
}

@media (max-width: 550px) {
  .content {
    margin-top: 0;
  }
}